import { axiosClient } from "@rp/react/axios-client"
import { useMutation } from "@tanstack/react-query"
import { ContactValues } from "./contactSales.types"

const CONTACT_PATH = "/marketing/sales-request"

const postContactSales = async (values: ContactValues) => {
  const { data } = await axiosClient.post<unknown>(CONTACT_PATH, values)
  return data
}

const useSubmitForm = () => {
  return useMutation({
    mutationFn: postContactSales,
  })
}

export { useSubmitForm }
